import React, { useState, useEffect } from "react";
import { VerticalAlignBottomOutlined } from "@ant-design/icons";
import ComPagination from "@/components/comPagination/ComPagination.jsx";
import {
  Input,
  Tabs,
  Menu,
  Pagination,
  Space,
  Table,
  Tag,
  Row,
  Col,
  Tooltip,
} from "antd";
import moment from "moment";
import { useTranslation } from "react-i18next";
import { NavLink, useLocation } from "react-router-dom";
import { folderList, fileList, downloadfile } from "../../../api/support";
// import "./help.css";
import "./filedownload.less";
export default function Filedownload() {
  const { TabPane } = Tabs;
  const [items, setitems] = useState([]);
  const [current, setCurrent] = useState("");
  const [total, settotal] = useState("");
  const [data, setdata] = useState([]);
  const [params, setParams] = useState({
    keywords: "",
    page: 1,
    limit: 20,
    orderby: "id",
    sort: "desc",
  });
  const columns = [
    {
      title: "文件名",
      dataIndex: "name",
      ellipsis: {
        showTitle: false,
      },
      width: "40%",
      render: (name) => (
        <>
          <Tooltip placement="topLeft" title={name}>
            {name}
          </Tooltip>
        </>
      ),
    },
    {
      width: "20%",
      title: "上传日期",
      dataIndex: "create_time",
      ellipsis: {
        showTitle: false,
      },
      render: (create_time) => (
        <>{moment(create_time * 1000).format("YYYY-MM-DD")}</>
      ),
    },
    {
      width: "15%",
      title: "文件类型",
      align: "center",
      dataIndex: "filetype",
    },
    {
      width: "12%",
      title: "大小",
      align: "center",
      dataIndex: "filesize",
      render: (filesize) => (
        <>
          {filesize / 1024 / 1024 >= 1
            ? (filesize / 1024 / 1024).toFixed(2) + "M"
            : parseInt(filesize / 1024) + "kb"}
        </>
      ),
    },
    {
      width: "10%",
      title: "操作",
      align: "center",
      className: "options",
      dataIndex: "action",
      render: (_, record) => (
        <div className="downloadicon">
          <VerticalAlignBottomOutlined
            onClick={() => getdownloadfile(record)}
          />
        </div>
      ),
    },
  ];
  const { SubMenu } = Menu;
  //decodeURIComponent 解密
  useEffect(() => {
    getmenulist();
    getfilelist();
  }, []);
  useEffect(() => {
    getfilelist();
  }, [params]);
  const tosearch = (e) => {
    let keywords = e.target.value;
    let page = 1;
    setParams((params) => {
      return { ...params, page, keywords };
    });
  };
  //获取文件列表
  const getfilelist = (id) => {
    console.log(current, "current");
    let param = {
      addon_idcsmart_file_folder_id: id,
      ...params,
    };
    fileList(param).then((res) => {
      settotal(res.data.data.count);
      setdata(res.data.data.list);
      console.log(res.data.data, "res.data.data.list");
    });
  };
  //文件下载
  const getdownloadfile = (item) => {
    downloadfile({ id: item.id }).then((res) => {
      console.log("file0", item, res);

      const fileName = item.name + "." + item.filetype;

      const _res = res.data;

      const blob = new Blob([_res]);

      const downloadElement = document.createElement("a");

      const href = window.URL.createObjectURL(blob); // 创建下载的链接

      downloadElement.href = href;

      downloadElement.download = decodeURI(fileName); // 下载后文件名

      document.body.appendChild(downloadElement);

      downloadElement.click(); // 点击下载

      document.body.removeChild(downloadElement); // 下载完成移除元素

      window.URL.revokeObjectURL(href); // 释放掉blob对象
    });
  };
  const checkMenu = (e) => {
    setCurrent(e.key);
    getfilelist(e.key);
    console.log(e.key, "e.key");
  };

  const searchlist = () => {
    getfilelist();
  };
  const getmenulist = () => {
    folderList().then((res) => {
      console.log(res.data.data.list, "res.data.data.list");
      setitems(res.data.data.list);
    });
  };
  const changePage = (page, limit) => {
    setParams({
      ...params,
      page,
      limit,
    });
    console.log(page, limit, params, "page");
  };
  return (
    <div className="file flex">
      <div className="file_top">
        <div className="bg_right"></div>
        <div className="file_toptext flex">
          <div className="file_leftname ">
            <div className="file_title">获取你所需文件</div>
            <div className="file_subtitle">{total}份文件</div>
          </div>
          <div className="file_searh flex align_center">
            <img src={require("@/assets/images/search.png")} />
            <Input
              placeholder="请输入"
              bordered={false}
              onPressEnter={(e) => searchlist(e)}
              onChange={tosearch}
            />
            <div className="file_searhbtn" onClick={searchlist}>
              搜索
            </div>
          </div>
        </div>
      </div>
      <div className="file_con flex">
        <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 30 }}>
          <Col xs={{ span: 24 }} lg={{ span: 18 }}>
            <div className="left_dowm">
              <div className="file_table">
                <Table columns={columns} dataSource={data} pagination={false} />
                <ComPagination
                  total={total}
                  page={params}
                  changePage={changePage}
                ></ComPagination>
              </div>
            </div>
          </Col>
          <Col xs={{ span: 24 }} lg={{ span: 6 }}>
            <div className="right_menu">
              <div className="menu_top">文档目录</div>
              <div className="menu_title">
                <Menu
                  mode="inline"
                  selectedKeys={current}
                  // items={items}
                  onClick={checkMenu}
                  className="Menu"
                >
                  {items.map((item, index) => {
                    // if (item.helps.length > 0) {
                    //   return (
                    //     <SubMenu key={item.id} title={item.name}>
                    //       {item.helps.map((node) => {
                    //         return (
                    //           <Menu.Item key={node.id}>{node.title}</Menu.Item>
                    //         );
                    //       })}
                    //     </SubMenu>
                    //   );
                    // }
                    return (
                      <Menu.Item key={item.id}>
                        <div className="menuitem flex">
                          <div className="menuitemname">{item.name}</div>
                          <div
                            className={`filenum  colornum${
                              index + 1 <= 4
                                ? index + 1
                                : (index + 1) % 4 !== 0
                                ? (index + 1) % 4
                                : 4
                            }`}
                          >
                            {item.file_num}
                          </div>
                        </div>
                      </Menu.Item>
                    );
                  })}
                </Menu>
              </div>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  );
}
